.chat-app {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: sans-serif;
    width: 90%;
    max-width: 1000px;
    margin: 25px auto;
    border-radius: 5px 5px 0 0;
    border: 3px solid #0079FF;
    height: 75vh;
    background-color: white;
  }
  .messages {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: 80%;
    overflow-y: auto;
    padding: 10px;
    margin-bottom: 10px;
  }

  .title{
    background-color: #0079FF;
    width: 100%;
    text-align: center;
    color: white;
    padding: 15px;
  }
  
  .message {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin-bottom: 10px;
    width: 100%;
  }
  
 
  .user {
    font-weight: bold;
    margin-right: 10px;
    color: #333;
  }

  
  .new-message-form {
    display: flex;
    width: 100%;
    padding: 10px 0 0 0
  }
  
  .new-message-input {
    flex: 1;
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    color: #333;
    border-top: 2px solid #0079FF;
    padding: 15px;
    background-color: white;
  }
  
  .send-button {
    border: none;
    border-top: 2px solid #0079FF;
    outline: none;
    background: #0079FF;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    padding: 10px;
  }
