@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;700&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  min-height: 100vh;
  background-color: #ddd;
}

.App {
  overflow-x: hidden;
  display: flex;
  align-items: center;
  width: 100%;
  flex-direction: column;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.header{
  padding: 25px;
  margin-bottom: 15px;
  font-family: 'Roboto Slab', serif;
  background-color: #0079FF;
  color: white;
  width: 100%;
  text-align: center;
  font-size: 1rem;
}

.header img{
  margin-right: 15px;
  width: 1.3rem;
}

.room {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.room label {
  text-align: center;
  font-size: 25px;
  margin-bottom: 20px;
  font-family: 'Roboto Slab', serif;
}

.room input {
  width: 200px;
  height: 30px;
  border: 2px solid #0079FF;
  border-radius: 6px;
  padding-left: 5px;
  font-size: 20px;
  text-align: center;
  margin: 5px;
}
.room button {
  width: 210px;
  height: 40px;
  border: none;
  border-radius: 6px;
  padding-left: 5px;
  font-size: 20px;
  text-align: center;
  margin: 5px;
  background-color: #0079FF;
  color: white;
  cursor: pointer;
}

.sign-out {
  margin-top: 50px;
}

.sign-out button {
  display: block;
  margin: auto;
  background-color: black;
  color: white;
  border: none;
  font-size: 1.2rem;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 1px solid white;
  border-radius: 15px;
  margin-bottom: 50px;
}

footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #0079FF;
  padding: 10px;
}

footer p{
  color: white;
  text-align: center;
  font-family: 'Roboto Slab', serif;
}

footer p a{
  color: black;
}

/* ===== Scrollbar CSS ===== */
  /* Firefox */
  * {
    scrollbar-width: auto;
    scrollbar-color: #000000 #ffffff;
  }

  /* Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 16px;
  }

  *::-webkit-scrollbar-track {
    background: #ffffff;
  }

  *::-webkit-scrollbar-thumb {
    background-color: #000000;
    border-radius: 0px;
    border: 3px solid #ffffff;
  }

@media (min-width: 1024px){
  .header{
    font-size: 1.6rem;
  }

  .header img{
    width: 1.7rem;
  }
}
