.auth {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    width: 100%;
}

.auth button {
    padding: 10px 10px;
    width: 300px;
    border: none;
    background-color: #0079FF;
    color: white;
    border-radius: 8px;
    font-size: 25px;
    cursor: pointer;
    margin-top: 25px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.auth svg{
    width: 30px;
    position: relative;
    top: 5px;
    height: 100%;
    display: inline-block;
}

.auth-text{
    font-weight: bold;
    font-size: 1.3rem;
}